@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,400;1,700&display=swap);
body{font-family:'Nunito'}*{box-sizing:border-box}body,html{margin:0;padding:0}p{margin:0;padding:0}#root{width:100%}.container{display:flex;flex-direction:column;align-items:stretch;margin:0rem auto;max-width:30rem}.main-header{display:flex;justify-content:space-between;align-items:center;width:100%;margin:1rem auto 1rem auto;padding:1rem;z-index:100}.main-header__brand{text-decoration:none;font-size:1.5rem}.task-list{flex-grow:1}.task-list svg{margin-right:0.5em}.task-list__header{font-size:1.5rem;display:flex;align-items:center;justify-content:flex-start;margin:1rem}.task{position:relative;display:flex;align-items:center;justify-content:space-between;margin:0.5rem 1rem;border-top:1px solid rgba(0,0,0,0.1);transition:transform .5s ease-out}.task:hover{transform:scale(1.03)}.task__body{display:flex;align-items:center;justify-content:flex-start;flex-grow:1;padding:1rem}.task__complete{display:flex;align-items:center;justify-content:center;margin-right:1rem}.task__icons{justify-self:flex-end;display:flex;align-items:center;justify-content:center;padding:0.5rem}.task__icons--delete{display:flex;align-items:center;justify-content:center;fill:#FF3263;transition:transform .5s ease-out;margin-left:1rem}.task__icons--edit{display:flex;align-items:center;justify-content:center;fill:#17D778;z-index:50}.edit-modal{position:fixed;left:50%;top:30%;transform:translate(-50%, 0);display:flex;align-items:center;justify-content:center;flex-direction:column;background:white;padding:1rem;z-index:150;border:1px solid black;border-radius:4px}.edit-modal form input{padding:0.5rem 0;margin:1rem 0;border:2px solid black;border-radius:4px;font-size:1.25rem}.edit-modal__buttons{display:flex;align-items:center;justify-content:center;margin-top:1rem}.edit-modal__buttons button{font-size:1.25rem}.backdrop{position:fixed;opacity:1;top:0;left:0;z-index:100;width:100vw;height:100vh;background:rgba(0,0,0,0.5);transition:opacity 0.2s linear}.add-task{width:100%;padding:0.5rem;display:flex;align-items:center;justify-content:center;margin-bottom:1rem}.add-task__input{display:flex;flex-direction:column;width:70%;margin:0.25rem auto}.add-task__input input{font-size:1.25rem;width:100%;padding:0.5rem;border:2px solid black;border-radius:4px}.add-task__icon{width:2rem}@media (min-width: 960px){.container{max-width:40rem;border-radius:1rem;margin-top:3rem;box-shadow:2px 2px 5px}}.btn{border:2px solid #FCD34D;background-color:#FCD34D;color:white;margin:0.25rem;padding:0.75rem;border-radius:5rem;cursor:pointer;text-decoration:none;outline:none;transition:background-color ease-out .5s, color ease-out .5s, transform .5s ease-out}.btn:hover{background:white;color:#FCD34D;transform:scale(1.1);box-shadow:2px 2px}.btn-header{min-width:2rem}.btn-add{display:flex;align-items:center;justify-content:center;width:3rem;height:3rem;align-self:flex-end;margin-right:1rem}

